<template>
    <el-container>
      <el-header>
        <el-button @click="officeW"></el-button>
        <el-button @click="officeV"></el-button>
      </el-header>
      <el-main style="overflow:hidden;">
        <vue-ueditor-wrap v-model="form.content" :config="quillEditorOptions" ref="myQuillEditor"></vue-ueditor-wrap>
      </el-main>
    </el-container>
</template>

<script>
import VueUeditorWrap from 'vue-ueditor-wrap'
import { office } from '@/api/office'
export default {
  name: 'office',
  components: {
    VueUeditorWrap
  },
  data () {
    return {
      form: {
        content: ''
      },
      quillEditorOptions: {
        // ,
        // serverUrl: this.$config.baseUrl + 'ueditor/ueditorConfig',
        serverUrl: 'http://localhost:8080/baidu/ueditor/ueditorConfig',
        // UEditor,index.html
        UEDITOR_HOME_URL: '/static/ueditor/',
        // 
        autoHeightEnabled: false,
        // 
        autoFloatEnabled: false,
        // 
        initialFrameHeight: 340,
        // 
        initialFrameWidth: '100%',
        // 
        enableAutoSave: true
      }
    }
  },
  methods: {
    officeV () {
      this.data.view = true
      office({
        html: this.from.content
      }).then(resp => {
        console.log(resp)
        window.open(resp.data)
      })
    },
    officeW () {
      this.data.view = false
      office(this.data).then(resp => {
        console.log(resp)
        const downloadElement = document.createElement('a')
        downloadElement.href = resp.data
        downloadElement.download = this.data.out // 
        document.body.appendChild(downloadElement)
        downloadElement.click()// 
        document.body.removeChild(downloadElement) // 
      })
    }
  }
}
</script>

<style scoped>

</style>



